:root {
  --container: 1172px;
  --main-font: "Lato", sans-serif;
  --raleway: "Raleway", sans-serif;
  --font-size: 18px;
  --color-1: #3E9CDC;
  --color-2: #000000;
  --color-3: #2A2B30;
  --color-4: #6F6D7B;
  --color-5: rgba(255, 255, 255, 0.85);
  --color-7: #BBBBBB;
  --white: #FFF;
  --bg-color: #272733;
  --radius-1: 4px;
  --radius-2: 3px;
}

html {
  font-size: var(--font-size);
  color: var(--white);
  lang: ru;
  font-variant-numeric: lining-nums proportional-nums;
}

html,
body {
  scrollbar-gutter: stable;
  background-color: var(--bg-color);
}

body {
  position: relative;
  max-width: 100vw !important;
  margin: 0;
}

body * {
  font-family: var(--main-font);
  font-weight: 400;
  line-height: 1;
}

main {
  position: relative;
  z-index: 3;
  min-height: 55vh;
  background-color: var(--bg-color);
}

main.layer-up {
  z-index: 5;
}

section {
  position: relative;
  z-index: 1;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  display: block;
  padding: 0;
  margin: 0;
}

strong,
b {
  font-weight: bold;
}

* {
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: border-box;
}

a,
a:hover,
a:active,
a:focus,
button,
input,
button:hover,
button:active,
button:focus,
input:focus,
input:active {
  text-decoration: none;
  outline: none;
}

a {
  color: inherit;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type=number],
input[type=number]:hover,
input[type=number]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

img,
picture,
figure {
  display: block;
  margin: 0;
}

.container {
  position: relative;
  max-width: var(--container);
  width: 100%;
  margin: 0 auto;
}

.validate {
  border-color: darkred !important;
}

.main-title,
.main-title * {
  font-size: 48px;
  font-weight: bold;
  text-transform: uppercase;
  user-select: none;
}

.second-title,
.second-title * {
  font-size: 36px;
  font-weight: 900;
  text-transform: uppercase;
  user-select: none;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-transform: uppercase;
  outline: none !important;
  border-radius: var(--radius-1);
  cursor: pointer;
  transition: background-color .3s ease-in-out,color .3s ease-in-out;
}

.btn.load {
  position: relative;
  pointer-events: none;
  background: #d8d8d8 !important;
}

.btn.load::after {
  animation-duration: .3s;
  animation-fill-mode: both;
  animation: rotateAnimation .3s linear infinite;
}

.btn.sm {
  height: 43px;
  padding: 15px 0;
}

.btn.md {
  height: 48px;
  padding: 17.5px 0;
}

.btn.blue {
  color: var(--white);
  background-color: var(--color-1);
  border: 1px solid var(--white);
}

.btn.blue:hover,
.btn.blue:focus-visible {
  background-color: #286690;
}

.btn.white {
  border: 1px solid var(--color-4);
  background-color: var(--white);
}

.btn.white:hover,
.btn.white:focus-visible {
  color: var(--white) !important;
  background-color: #939393;
}

.blue-text {
  color: var(--color-1) !important;
}

.tablet {
  display: none !important;
}

.mobile {
  display: none !important;
}

.form__row-checkboxes {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.form__col {
  position: relative;
}

.form__col .msg {
  position: absolute;
  bottom: 0;
  left: 10px;
  display: none;
  width: max-content;
  font-size: 10px;
  color: red;
  letter-spacing: .1em;
  transform: translateY(100%);
}

.form-input {
  width: 100%;
  height: 58px;
  padding: 17px 20px;
  font-size: var(--font-size);
  color: var(--white);
  background-color: rgba(0,0,0,0);
  border: 1px solid var(--white);
  border-radius: var(--radius-2);
  transition: border-color .3s ease-in-out;
}

.form-input:hover,
.form-input:focus-visible {
  border-color: var(--color-4);
}

.loading::before {
  content: "";
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  background: hsla(0,0%,100%,.6) url("../img/icons/spinner.svg") center/100px no-repeat;
}

.disabled {
  pointer-events: none;
  background: #d8d8d8 !important;
}

.hidden {
  display: none !important;
}

.form-label {
  position: relative;
}

.form-label input::placeholder,
.form-label textarea::placeholder {
  color: rgba(0,0,0,0);
}

.form-label input.show-placeholder::placeholder,
.form-label textarea.show-placeholder::placeholder {
  color: inherit;
}

.form-label input~label .red,
.form-label textarea~label .red {
  color: red !important;
}

.form-label input:focus~label,
.form-label input:active~label,
.form-label input.filled~label,
.form-label input:valid~label,
.form-label textarea:focus~label,
.form-label textarea:active~label,
.form-label textarea.filled~label,
.form-label textarea:valid~label {
  color: #fff;
  background-color: var(--color-3);
  transform: translateY(-70%);
}

.form-label .transform-label {
  position: absolute;
  top: 0;
  left: 10px;
  padding: 5px;
  color: var(--bg-color);
  outline: none;
  border-radius: 6px;
  background-color: rgba(0,0,0,0);
  transform: translateY(10px);
  pointer-events: none;
  transition: transform .3s ease-in-out,color .3s ease-in-out,background-color .3s ease-in-out;
}

.form-label .transform-label,
.form-label .transform-label * {
  height: calc(var(--font-size)*1.625);
  font-size: var(--font-size);
  line-height: 1;
  cursor: auto;
}

.form-field {
  width: 100%;
  height: 48px;
  padding: 10px;
  font-size: var(--font-size);
  color: var(--bg-color);
  border-radius: var(--radius-2);
  border: 2px solid rgba(0,0,0,0);
  transition: border-color .3s ease-in-out;
}

.form-field:hover,
.form-field:focus-visible {
  border-color: var(--color-1);
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

@keyframes slideInLeft {
  from {
    transform: translate3d(-100%, 0, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInLeft {
  animation-name: slideInLeft;
}

@keyframes slideInUp {
  from {
    transform: translate3d(0, 100%, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInUp {
  animation-name: slideInUp;
}

@keyframes slideOutLeft {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(-100%, 0, 0);
  }
}

.slideOutLeft {
  animation-name: slideOutLeft;
}

@keyframes slideOutUp {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(0, -100%, 0);
  }
}

.slideOutUp {
  animation-name: slideOutUp;
}

@keyframes slideOutRight {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(100%, 0, 0);
  }
}

.slideOutRight {
  animation-name: slideOutRight;
}

@keyframes slideInRight {
  from {
    transform: translate3d(100%, 0, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInRight {
  animation-name: slideInRight;
}

@keyframes slideOutDown {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(0, 100%, 0);
  }
}

.slideOutDown {
  animation-name: slideOutDown;
}

@keyframes slideInDown {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInDown {
  animation-name: slideInDown;
}

@keyframes rotateAnimation {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

select.custom-select {
  width: 100%;
  height: 48px;
  padding: 10px;
  font-size: var(--font-size);
  color: var(--bg-color);
  background-color: var(--white);
  border: 2px solid rgba(0,0,0,0);
  outline: none;
  border-radius: var(--radius-2);
  transition: border-color .3s ease-in-out;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

select.custom-select:hover,
select.custom-select:focus-visible {
  border-color: var(--color-1);
}

select.custom-select option {
  padding: 10px 15px;
  font-size: var(--font-size);
  border-color: rgba(0,0,0,0);
  border-radius: 0;
  background-color: var(--bg-color);
  color: var(--white);
  transition: background-color .3s ease-in-out;
}

select.custom-select option:hover,
select.custom-select option:focus-visible {
  background-color: var(--color-2-hover);
}

.custom-select {
  position: relative;
}

.custom-select:hover .select-title,
.custom-select:focus-visible .select-title {
  border-color: var(--color-1);
}

.custom-select:hover .select-content,
.custom-select:focus-visible .select-content {
  border-color: var(--color-1);
}

.custom-select.open .select-title {
  border-bottom-color: rgba(0,0,0,0);
  border-radius: var(--radius-2) var(--radius-2) 0 0;
}

.custom-select.open .select-title .select-title__icon {
  transform: rotate(180deg);
}

.custom-select.open .select-content {
  opacity: 1;
  transform: scale(1);
}

.custom-select.active .select-title {
  color: var(--white);
}

.custom-select.initialized::after {
  content: none;
}

.custom-select::after {
  content: "▼";
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  pointer-events: none;
  font-size: 12px;
  color: var(--white);
}

.select-title {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 48px;
  padding: 10px;
  font-size: var(--font-size);
  color: var(--white);
  border: 2px solid #3d4050;
  border-radius: var(--radius-2);
  background-color: #393b4b;
  cursor: pointer;
  overflow: hidden;
  transition: border-color .3s ease-in-out;
}

.select-title:hover,
.select-title:focus-visible {
  border-color: var(--color-1);
}

.select-title>* {
  position: relative;
  z-index: 1;
}

.select-title__text {
  flex-shrink: 0;
  font-size: inherit;
  color: inherit;
  line-height: inherit;
  user-select: none;
}

.select-title__icon {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  margin-right: -4px;
  color: inherit;
  transition: transform .3s ease-in-out;
}

.select-title__icon svg {
  transition: color .3s ease-in-out;
}

.select-content {
  position: absolute;
  top: calc(100% - 2px);
  right: 0;
  left: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  width: calc(100% - 1px);
  height: max-content;
  border: 2px solid #3d4050;
  border-top: none;
  border-radius: 0 0 var(--radius-2) var(--radius-2);
  background-color: var(--bg-color);
  backdrop-filter: blur(10px);
  opacity: 0;
  transform: scale(0);
  transition: opacity .3s ease-in-out,border-color .3s ease-in-out;
  overflow: hidden;
}

.select-content.no-scroll .select-content_wrap {
  overflow-y: hidden;
}

.select-content_overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  margin: auto;
  z-index: 99;
  background-color: rgba(0,0,0,0);
  backdrop-filter: blur(10px);
}

.select-content_wrap {
  padding: 10px 0;
  overflow-y: scroll;
  scrollbar-width: thin;
  scrollbar-color: var(--color-1) #3d4050;
}

.select-content_wrap::-webkit-scrollbar-thumb {
  background: var(--color-1);
  border-radius: 3px;
}

.select-content_wrap::-webkit-scrollbar {
  width: 6px;
}

.select-content_wrap::-webkit-scrollbar-track {
  background: #3d4050;
  border-radius: 3px;
}

.select-content__wrapper {
  position: relative;
  display: flex;
  cursor: pointer;
  transition: background-color .3s ease-in-out;
}

.select-content__wrapper:focus-visible .select-content__radio,
.select-content__wrapper:hover .select-content__radio {
  color: var(--color-1);
}

.select-content__wrapper.disabled {
  pointer-events: none;
}

.select-content__wrapper input {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.select-content__wrapper input:checked~.select-content__radio {
  color: #fff !important;
  background-color: var(--green) !important;
}

.select-content__wrapper input:disabled {
  pointer-events: none;
}

.select-content__wrapper input:disabled~.select-content__radio {
  color: #fff !important;
  background-color: gray !important;
  pointer-events: none;
}

.select-content__radio {
  width: 100%;
  padding: 10px;
  color: #fff;
  user-select: none;
}

@keyframes SelectContentZoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}

.zoomIn {
  animation-name: zoomIn;
}

@keyframes SelectContentZoomOut {
  from {
    opacity: 1;
  }

  50% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }

  to {
    opacity: 0;
  }
}

.zoomOut {
  animation-name: zoomOut;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 4;
  height: 100px;
  padding-top: 33.5px;
  background-color: rgba(16,16,29,.9);
  transition: background-color .3s ease-in-out;
}

.header._scroll {
  background-color: #10101d;
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  user-select: none;
}

.header__logo,
.header__logo * {
  font-family: var(--raleway);
  font-weight: 800;
  font-size: 28px;
  text-transform: uppercase;
  color: var(--white);
}

.header__nav-list {
  display: flex;
  align-items: center;
  gap: 60px;
}

.header__nav-item.active .header__nav-item__link {
  color: var(--color-1);
}

.header__nav-item__link {
  color: var(--white);
  font-weight: 600;
  font-size: 13px;
  text-transform: uppercase;
  transition: color .3s ease-in-out;
}

.header__nav-item__link:hover,
.header__nav-item__link:focus-visible {
  text-decoration: underline;
}

.header__close {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  color: var(--white);
}

.header__hamburger.open .line {
  position: absolute;
  inset: 0;
  margin: auto;
}

.header__hamburger.open .line-1 {
  transform: rotate(45deg);
}

.header__hamburger.open .line-2 {
  opacity: 0;
}

.header__hamburger.open .line-3 {
  transform: rotate(-45deg);
}

.header__hamburger .line {
  width: 100%;
  height: 2px;
  background-color: var(--white);
  border-radius: 10px;
  transition: transform .3s ease-in-out;
}

.header__hamburger .line-2 {
  width: 70%;
}

.nav_scroll .menu {
  background-color: var(--bg-color);
}

.nav_scroll .menu .menu__cart {
  display: flex;
}

.menu {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
  width: 100%;
  border-bottom: 1px solid hsla(0,0%,100%,.1);
  background-color: var(--bg-color);
  overflow-x: hidden;
}

.menu .container {
  display: flex;
  align-items: center;
  gap: 30px;
}

.menu .menu__nav {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  overflow-x: hidden;
}

.menu .menu__nav .homepage_menu {
  width: calc(var(--container) + (100vw - var(--container))/2);
}

.menu .menu__nav::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 176px;
  height: 24px;
  margin: auto;
  background: linear-gradient(270deg, #403c3b 0%, rgba(70, 66, 65, 0) 100%);
}

.menu .menu__nav .nav-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  padding-right: 226px;
  overflow-x: scroll;
  scrollbar-width: none;
}

.menu .menu__nav .nav-list::-webkit-scrollbar {
  width: 0;
}

.menu .menu__nav .nav-list li {
  position: relative;
  flex-shrink: 0;
  height: 78px;
  user-select: none;
}

.menu .menu__nav .nav-list li::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: rgba(0,0,0,0);
  transition: background-color .3s ease;
}

.menu .menu__nav .nav-list li.active::after {
  background-color: var(--green);
}

.menu .menu__nav .nav-list li.active a {
  color: #fff;
}

.menu .menu__nav .nav-list li a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: #cfcfcf;
  font-size: 18px;
  line-height: normal;
  user-select: none;
  transition: color .3s ease-in-out;
}

.menu .menu__nav .nav-list li a::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.menu .menu__icon-scroll {
  z-index: 1;
  flex-shrink: 0;
}

.menu .menu__icon-scroll.hide,
.menu .menu__icon-scroll.hide-on-scroll {
  display: none !important;
}

.menu .menu__cart {
  display: none;
}

.hero {
  max-width: 1920px;
  margin: 0 auto;
  padding: 203px 0 202px;
  color: var(--color-2);
  background: var(--white) url("../img/hero-bg.png") center/auto no-repeat;
  background-size: cover;
  background-color: #fff;
}

.hero__title {
  max-width: 780px;
  margin-bottom: 80px;
}

.hero__text {
  max-width: 780px;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 20px;
}

.hero__list {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin-bottom: 80px;
}

.hero__list-item {
  display: flex;
  gap: 1px;
  font-size: 20px;
}

.hero__list-item::before {
  content: "-";
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 22px;
  transform: translateY(-2.5px);
}

.hero__btn-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}

.hero__btn {
  width: 183px;
  font-size: 13px;
  text-transform: uppercase;
}

.hero__btn._more {
  color: #2a2b30;
}

.checkout {
  padding: 120px 0 150px;
}

.checkout__title {
  margin-bottom: 30px;
  text-align: center;
}

.checkout__text {
  max-width: 555px;
  margin: 0 auto 80px;
  text-align: center;
  color: var(--color-7);
}

.checkout__scheme {
  display: flex;
  gap: 30px;
  margin-bottom: 80px;
}

.checkout__scheme-item {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  width: calc(20% - 24px);
  padding: 0 10px;
}

.checkout__scheme-item:not(:last-child)::after {
  content: "";
  position: absolute;
  top: 48px;
  right: 0;
  width: 6px;
  height: 6px;
  background-color: #c4c4c4;
  border-radius: 50%;
  box-shadow: 20px 0 #c4c4c4,40px 0 #c4c4c4,60px 0 #c4c4c4,80px 0 #c4c4c4;
  transform: translateX(-22px);
}

.checkout__scheme-item .item-icon {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  border: 5px solid #286690;
  border-radius: 50%;
  background-color: var(--color-1);
}

.checkout__scheme-item .item-text {
  text-align: center;
  font-size: 16px;
}

.checkout__form {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.checkout__form .form-row {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}

.checkout__form .form-row._submit {
  width: 370px;
  margin: 0 auto;
}

.checkout__form .form-label {
  width: calc(33.33% - 20px);
}

.checkout__form .form-label._range {
  width: calc(66.66% - 10px);
}

.checkout__form .form-label._range .range-slider {
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 100%;
  padding: 20px 0 30px;
}

.checkout__form .form-label._range .range-label {
  display: flex;
  justify-content: space-between;
  height: max-content;
  padding: 0;
  color: var(--white);
  transform: none;
}

.checkout__form .form-label._range .range-label-text {
  position: static;
  padding: 0;
  font-size: var(--font-size);
  color: var(--white);
  transform: none;
}

.checkout__form .form-label._range .range-label-value {
  font-weight: 600;
}

.checkout__form .form-label._range .range-input {
  -webkit-appearance: none;
  width: 100%;
  height: 6px;
  border: 1px solid var(--white);
  border-radius: 5px;
  background-color: hsla(0,0%,100%,.6980392157);
  outline: none;
}

.checkout__form .form-label._range .range-input::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 28px;
  height: 28px;
  border: 3px solid var(--color-1);
  border-radius: 50%;
  background-color: #42a9ed;
  cursor: pointer;
  transition: border-color .3s ease-in-out;
}

.checkout__form .form-label._range .range-input::-webkit-slider-thumb:hover,
.checkout__form .form-label._range .range-input::-webkit-slider-thumb:focus-visible {
  border-color: var(--white);
}

.checkout__form .form-label._range .range-input::-moz-range-thumb {
  width: 28px;
  height: 28px;
  border: 3px solid var(--color-1);
  border-radius: 50%;
  background-color: #42a9ed;
  cursor: pointer;
  transition: border-color .3s ease-in-out;
}

.checkout__form .form-label._range .range-input::-moz-range-thumb:hover,
.checkout__form .form-label._range .range-input::-moz-range-thumb:focus {
  border-color: var(--white);
}

.checkout__form .form-label._file .file-output {
  position: relative;
  width: max-content;
}

.checkout__form .form-label._file .file-output__icon svg {
  width: 48px;
  height: 48px;
}

.checkout__form .form-label._file .file-output__remove {
  position: absolute;
  top: 0;
  right: 0;
  width: 26px;
  height: 26px;
  border: 2px solid rgba(0,0,0,0);
  border-radius: 50%;
  background-color: var(--white);
  cursor: pointer;
  transform: translate(35%, -35%);
  transition: border-color .3s ease-in-out;
}

.checkout__form .form-label._file .file-output__remove:hover,
.checkout__form .form-label._file .file-output__remove:focus-visible {
  border-color: var(--color-1);
}

.checkout__form .form-label._file .file-output__remove:hover::after,
.checkout__form .form-label._file .file-output__remove:hover::before,
.checkout__form .form-label._file .file-output__remove:focus-visible::after,
.checkout__form .form-label._file .file-output__remove:focus-visible::before {
  background-color: var(--color-1);
}

.checkout__form .form-label._file .file-output__remove::after,
.checkout__form .form-label._file .file-output__remove::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 12px;
  height: 2px;
  background-color: #000;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: background-color .3s ease-in-out;
}

.checkout__form .form-label._file .file-output__remove::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.checkout__form .form-label._file .file-upload__text {
  display: flex;
  align-self: center;
  gap: 10px;
  color: var(--bg-color);
  font-size: 15px;
  font-weight: 600;
}

.checkout__form .form-label._file .file-upload__text-icon {
  color: inherit;
  transition: color .3s ease-in-out;
}

.footer {
  padding: 30px 0;
  color: var(--white);
  background-color: #3d4050;
}

.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__col._separator {
  width: 1px;
  height: 18px;
  background-color: #c3c3c3;
}

.footer__copyright {
  font-size: 13px;
}

.footer__payments-list,
.footer ._col-3 {
  display: flex;
  align-items: center;
  gap: 38px;
}

.footer__payments-list__item a,
.footer__contact {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 13px;
}

.footer__payments-list__item a:hover,
.footer__payments-list__item a:focus-visible,
.footer__contact:hover,
.footer__contact:focus-visible {
  text-decoration: underline;
}

.footer__payments-list__item a .svg-ico,
.footer__contact .svg-ico {
  color: var(--color-1);
}

@media screen and (max-width: 1200.98px) {
  :root {
    --container: 100vw;
  }

  .container {
    padding: 0 20px;
  }
}

@media screen and (max-width: 1029.98px) {
  :root {
    --container: 100vw;
  }

  .btn {
    padding: 14px 0;
  }

  .desktop {
    display: none !important;
  }

  .tablet {
    display: block !important;
  }

  .header__nav-list {
    gap: 30px;
  }

  .menu .menu__icon-scroll {
    width: 35px;
    height: 35px;
  }

  .hero {
    padding: 150px 0;
  }

  .hero__title {
    max-width: 750px;
    margin-bottom: 40px;
  }

  .hero__text {
    max-width: 750px;
  }

  .hero__list {
    margin-bottom: 40px;
  }

  .hero__btn-wrapper {
    gap: 20px;
  }

  .checkout {
    padding: 80px 0 100px;
  }

  .checkout__text {
    margin-bottom: 40px;
  }

  .checkout__scheme {
    margin-bottom: 40px;
  }

  .checkout__scheme-item:not(:last-child)::after {
    box-shadow: 20px 0 #c4c4c4,40px 0 #c4c4c4;
    transform: translateX(-2px);
  }

  .footer .container {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }

  .footer__col._separator {
    width: 100%;
    height: 1px;
  }
}

@media screen and (max-width: 807.98px) {
  :root {
    --font-size: 14px;
  }

  .main-title,
  .main-title * {
    font-size: 34px;
  }

  .second-title,
  .second-title * {
    font-size: 34px;
  }

  .btn {
    height: 34px;
    padding: 5.5px 0 7.5px;
  }

  .desktop-tablet {
    display: none !important;
  }

  .mobile {
    display: block !important;
  }

  .form-input {
    height: 50px;
    padding: 20px 14.5px;
  }

  .form-label input:focus~label,
  .form-label input:active~label,
  .form-label input.filled~label,
  .form-label input:valid~label,
  .form-label textarea:focus~label,
  .form-label textarea:active~label,
  .form-label textarea.filled~label,
  .form-label textarea:valid~label {
    transform: translateY(-60%);
  }

  .form-label .transform-label {
    left: 11px;
    transform: translateY(13.5px);
  }

  .form-label .transform-label,
  .form-label .transform-label * {
    height: 23px;
    font-size: 13px;
  }

  .select-title {
    height: 50px;
    padding: 0 16px;
    font-size: 14px;
  }

  .select-content__radio {
    font-size: 14px;
  }

  .header {
    height: max-content;
    padding: 20px 0;
  }

  .header__logo,
  .header__logo * {
    font-size: 20px;
  }

  .header__nav {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    max-width: 375px;
    height: 100vh;
    width: 100%;
    transform: scale(0);
    transition: transfrom .3s ease-in-out;
  }

  .header__nav-list {
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 80px 20px 30px;
    background-color: #041129;
  }

  .header__hamburger {
    position: relative;
    display: flex !important;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    width: 30px;
    height: 26px;
    cursor: pointer;
  }

  .menu:not(.homepage_menu) {
    display: none;
  }

  .menu .menu__nav .nav-list {
    gap: 25px;
    padding-right: 0;
  }

  .menu .menu__nav .nav-list li {
    height: 80px;
  }

  .menu .menu__nav .nav-list li::after {
    bottom: 0;
  }

  .menu .menu__nav .nav-list li a {
    font-size: 14px;
  }

  .hero {
    padding: 100px 0;
  }

  .hero__title {
    max-width: 80%;
    margin-bottom: 30px;
  }

  .hero__text {
    max-width: 80%;
    font-size: 16px;
  }

  .hero__list {
    max-width: 80%;
    margin-bottom: 30px;
  }

  .hero__list-item {
    font-size: 16px;
  }

  .checkout {
    padding: 60px 0 80px;
  }

  .checkout__title {
    margin-bottom: 20px;
  }

  .checkout__text {
    margin-bottom: 20px;
  }

  .checkout__scheme {
    flex-direction: column;
    gap: 20px;
    counter-set: step;
  }

  .checkout__scheme-item {
    flex-direction: row;
    gap: 10px;
    width: 100%;
  }

  .checkout__scheme-item:not(:last-child)::after {
    content: none;
  }

  .checkout__scheme-item .item-icon {
    width: 60px;
    height: 60px;
  }

  .checkout__scheme-item .item-icon svg {
    width: 35px;
    height: 35px;
  }

  .checkout__scheme-item .item-text {
    text-align: left;
  }

  .checkout__scheme-item .item-text::before {
    content: "0" counter(step) ". ";
    counter-increment: step;
  }

  .checkout__form .form-row {
    gap: 20px;
  }

  .checkout__form .form-label {
    width: calc(50% - 10px);
  }

  .checkout__form .form-label._select {
    width: 100%;
  }

  .checkout__form .form-label._range {
    width: calc(50% - 10px);
  }

  .checkout__form .form-label._range .range-slider {
    gap: 24px;
    padding: 0;
  }

  .checkout__form .form-label._range .range-input::-webkit-slider-thumb {
    width: 20px;
    height: 20px;
  }

  .checkout__form .form-label._range .range-input::-moz-range-thumb {
    width: 20px;
    height: 20px;
  }

  .checkout__form .form-label._file {
    width: calc(50% - 10px);
  }
}

@media screen and (max-width: 575.98px) {
  .custom-select.open .select-content_wrap {
    animation-name: SelectContentZoomIn;
  }

  .select-content {
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 100;
    width: 100vw;
    max-height: none !important;
    height: 100vh;
    margin: auto;
    padding: 0;
    border: 0;
    border-radius: 0;
    backdrop-filter: none;
    transition: none;
  }

  .select-content_wrap {
    z-index: 100;
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: calc(100vw - 40px);
    max-height: calc(100vh - 40px);
    height: max-content;
    margin: auto;
    padding: 20px 10px;
    border: 1px solid hsla(0,0%,100%,.7);
    border-radius: 10px;
    animation-duration: .5s;
    animation-fill-mode: both;
    animation-name: SelectContentZoomOut;
  }

  .select-content__radio {
    background-color: rgba(73,69,68,.3);
    border: 1px solid hsla(0,0%,100%,.5);
    border-radius: 7px;
  }

  .menu .container {
    gap: 15px;
  }

  .checkout__form .form-row._submit {
    width: 100%;
  }

  .checkout__form .form-label {
    width: 100%;
  }

  .checkout__form .form-label._range {
    width: 100%;
  }

  .checkout__form .form-label._file {
    width: 100%;
  }

  .footer__payments-list,
  .footer ._col-3 {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }
}